* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-size: 20px;
  text-transform: uppercase;
}
header {
  background-size: cover;
  background-color: rgb(22, 21, 21);
  background-repeat: no-repeat;
  height: 100vh;
  opacity: 0.95;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 89%, 0 70%);
          clip-path: polygon(0 0, 100% 0, 100% 89%, 0 70%);
}
.logo {
  font-family: "Montserrat Subrayada", sans-serif;
  color: rgb(224, 224, 224);
}
.nav-header {
  display: flex;
  padding: 30px;
}
.main-nav {
  margin-left: 40px;
  float: right;
  margin-left: 700px;
}
.main-nav li {
  text-decoration: none;
  display: inline-block;
  padding: 10px;
  font-size: 90%;
  font-family: "Open Sans", sans-serif;
}
.main-nav li a {
  text-decoration: none;
  font-weight: 600;
  color: rgb(224, 224, 224);
  font-size: 18px;
}
.main-nav li a:hover {
  border-bottom: 2px solid #bf55ec;
}
.main-content {
  text-align: center;
  font-size: 30px;
  color: rgb(224, 224, 224);
  padding-top: 17vh;
}
.intro h4 {
  padding-top: 20px;
  font-size: 24px;
}
.intro h2 {
  font-size: 58px;
}
.color {
  -webkit-animation: colorChange 1s infinite;
          animation: colorChange 1s infinite;
}
@-webkit-keyframes colorChange {
  0% {
    color: red;
  }
  5% {
    color: blue;
  }
  20% {
    color: crimson;
  }
  25% {
    color: #bf55ec;
  }
  50% {
    color: aqua;
  }
  100% {
    color: green;
  }
}
@keyframes colorChange {
  0% {
    color: red;
  }
  5% {
    color: blue;
  }
  20% {
    color: crimson;
  }
  25% {
    color: #bf55ec;
  }
  50% {
    color: aqua;
  }
  100% {
    color: green;
  }
}

.card {
  width: 28rem;
}
.card:hover {
  box-shadow: 5px 10px 20px 1px rgba(0, 0, 0, 0.253) !important;
}
.card-body {
  padding: 3rem 0 !important;
}
.card-text {
  font-size: 0.9rem;
  padding-block: 0.4rem 1.9rem;
  font-family: "Open Sans Condensed", sans-serif;
}
.container-fluid.row {
  padding-top: 6rem;
}

